@charset "utf-8";

* {margin: 0; padding: 0;}

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,100..900;1,100..900&display=swap');

body {
  font-family: 'Noto Sans', sans-serif;
  font-size: 13px;
  color: #333;
  background-color: #eff3f7;
}

a {
  text-decoration: none;
  color: #333;
  font-size: 16px;
}

ul, li, ol {list-style: none;}

/* 공통서식 */

.header {
  width: 200px;
  height: 100%;
  text-align: center;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 100;
}

h1 {
  padding: 70px 0 50px 0;
}


.navi {
  padding-top: 70px;
  text-align: center;
  line-height: 40px;
}

.navi a {
  text-transform: uppercase;
  font-weight: bold;
}

.navi a:hover {text-decoration: underline;}

.navi a {
  text-decoration: none;
  font-size: 16px;
  padding: 5px 10px;
  color: #5d5d5d;
  text-shadow: 0px 1px 1px #adadad;
}

/* footer서식 */
.footer {
  width: 100%;
  line-height: 40px;
  background: #333;
  position: fixed; bottom: 0px;
}

.footer address {
  color: #fff;
  text-align: center;
  font-style: normal;
}

main {
  width: calc(100% - 200px);  height: 100%;
  position: fixed;  left: 200px;  top: 0px;
  padding-top: 50px;
  overflow: scroll;
}

/* 가로 해상도 1025px일 경우 서식 */
@media screen and (min-width:1025px) {}

/* 가로 해상도 768px ~ 1024px일 경우 서식 */
@media screen and (min-width:768px) and (max-width: 1024px) {}

/* 모바일 서식: 최대값 해상도 */
@media screen and (max-width: 767px) {}