@charset "utf-8";
.loadingWrap{
  width: 100%; height: 100%;
  position: relative;
}
.loadingWrap > .loading{
  position: absolute;
  left: 50%;top: 40%;
  transform: translate(-50%,-50%);
}
.movieWrap{
  display: grid;
  grid-template-columns: repeat(2, 50%);
  padding-bottom: 50px;
}
.movieCont{
  background-color: #fff;box-sizing: border-box;
  margin: 20px 30px;
  display: flex;
  padding: 10px 20px;
  box-shadow: 0px 4px 5px rgba(0,0,0,.15);
  position: relative;
}
.movieCont > .poster img{
  perspective: 800px;   /* 입체감의 핵심 */
  width: 200px;
  margin-right: 30px;
  transform: translatey(-30px);
  box-shadow: 0px 10px 10px rgba(0,0,0,.4);
  transition: 0.3s ease;
  transform-style: preserve-3d;
}
.movieCont > .poster img:hover{
  transform: translatey(0px);
}
.movieCont > .movieInfo{
  line-height: 30px;
}
.movieCont > .movieInfo p:first-of-type{
  color: #77C557;
}
.movieCont > .movieInfo p:nth-of-type(3){
  line-height: 20px;
}

.movieCont > .movieInfo span{
  position: absolute;
  right: 20px;
  bottom: 30px;
}